$primaryColor: #4527a4;
$secondaryColor: #8344c5;

.block-editor-block-list__block[data-type='csb/content-slider-block'][data-align='wide'] {
	max-width: 1440px !important;
	width: 94% !important;
	margin: 0 auto;

	@media screen and (max-width: 1366px) {
		width: 96% !important;
	}

	@media screen and (max-width: 768px) {
		width: 98% !important;
	}

	.wp-block {
		max-width: 100% !important;
		width: 100% !important;
	}
}

.csbUpgradeProToggle {
	.components-form-toggle {
		opacity: .75;
	}
}

.csbProGroupBtn {
	position: relative;

	&::before {
		content: 'Pro';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: #fff;
		background: $primaryColor;
		padding: 2px 8px;
		border-radius: 50px;
		z-index: 10;
		display: none;
	}

	&:hover::before {
		display: block;
	}

	svg {
		opacity: .75;
	}
}

.csbMutedText {
	opacity: .75;
}

.csbUpgradePro {
	color: #fff;
	background: $primaryColor;
	padding: 2px 8px;
	margin: 0 0 0 5px;
	border-radius: 50px;
	cursor: pointer;
}

.csbUpgradeProText {
	font-size: 15px;
}

.csbUpgradeProLearnMore {
	display: inline-block;
	font-size: 16px;
	font-weight: 500;
	color: #fff;
	background-color: $primaryColor;
	text-decoration: none;
	padding: 12px 20px;
	margin-top: 20px;
	border-radius: 50px;

	&:hover,
	&:focus {
		color: #fff;
		background-color: $secondaryColor;
	}
}

// Content Slider Block
.wp-block-csb-content-slider-block {
	.csbContentSlider {
		.swiper-slide {

			.sliderTitle,
			.sliderDescription,
			.sliderBtn {
				cursor: move;
			}
		}
	}
}